$link-color: $main-color;
$link-color-hover: $main-color-dark;

*, *:before, *:after {
  box-sizing: border-box; }

body {
  @extend %system-font;
  @extend %font-size-m;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  color: $color-black;

  &:before {
    top: auto;
    bottom: 0; } }

img {
  max-width: 100%; }

a {
  color: $link-color;

  &:hover {
    color: $link-color-hover; } }

hr {
  border: 0;
  border-bottom: 1px solid rgba($color-black, .2); }
