%helvetica {
  font-family: 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  font-weight: 400;
  font-style: normal; }

%system-font {
  font-family: 'Helvetica Neue', 'Helvetica', 'Arial', 'PingFang TC', 'Microsoft JhengHei', sans-serif;
  font-weight: 400;
  font-style: normal; }

%font-size-ui-s, %font-size-s {
  font-size: 0.75rem;
  line-height: 1.5;

  @include tablet {
    font-size: 0.85rem; } }

%font-size-ui-m, %font-size-m {
  font-size: 0.85rem;
  line-height: 1.5;

  @include tablet {
    font-size: 1rem; } }

%font-size-ui-mp, %font-size-mp {
  font-size: 1rem;
  line-height: 1.5;

  @include tablet {
    font-size: 1.25rem; } }

%font-size-ui-l, %font-size-l {
  font-size: 1.25rem;
  line-height: 1.5;

  @include tablet {
    font-size: 1.5rem; } }

%font-size-ui-xl, %font-size-xl {
  font-size: 1.5rem;
  line-height: 1.5;

  @include tablet {
    font-size: 1.8rem; } }
