.event-preview {
  margin: 24px auto;
  text-align: center;

  .screen-switch {
    display: none; }

  .screen-switch-button {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0 8px;
    width: 80px;
    height: 40px;
    background-color: $sub-color-gray-light;
    border-radius: 20px;

    &::before {
      content: '';
      position: absolute;
      top: 4px;
      left: 4px;
      width: 32px;
      height: 32px;
      background-color: $main-color;
      border-radius: 50%;
      transition: left .25s; } }

  .screen-preview {
    display: block;
    margin: 24px auto;
    width: 375px;
    height: 667px;
    border: 1px solid $sub-color-gray-bright;
    border-radius: $border-radius;
    transition: width .5s, height .5s; }

  .screen-switch:checked ~ .screen-preview {
    width: 1136px;
    height: 768px; }

  .screen-switch:checked ~ .screen-switch-button {
    &::before {
      left: 44px; } } }
