.form-section {
  margin-bottom: 48px;

  .aside {
    padding-bottom: 12px; }

  @include tablet {
    display: flex;

    .aside {
      flex: 1; }

    .main {
      flex: 8; } }

  .form-group {
    padding-top: 12px;
    border-top: 3px solid $sub-color-gray-bright;

    @include tablet {
      padding-top: 1px;
      padding-left: 24px;
      border-top: 0;
      border-left: 3px solid $sub-color-gray-bright; } } }


.form-title {
  margin: 0 12px 0 0;
  @extend %font-size-l;
  color: $sub-color-black-light;

  br {
    display: none;

    @include tablet {
      display: block; } } }

.form-group {
  @extend %font-size-ui-m;

  .form-unit + .form-unit {
    margin-top: 8px;

    @include tablet {
      margin-top: 16px; } }

  .label {
    @extend %font-size-ui-s;
    font-weight: 700;
    color: $sub-color-gray;

    &::after {
      content: ' / '; } }

  input[type='text'], input[type='password'], input[type='number'] {
    display: block;
    margin-top: 4px;
    padding: 4px 8px;
    max-width: 320px;
    width: 100%;
    height: 28px;
    @extend %font-size-ui-s;
    border-radius: $border-radius;
    border: 1px solid $sub-color-gray-bright;

    &.long-data {
      max-width: 480px; }

    &:focus {
      outline: 0;
      border-color: $sub-color-black-light; } }

  textarea {
    display: block;
    margin-top: 4px;
    padding: 8px;
    max-width: 480px;
    width: 100%;
    min-height: 120px;
    border-radius: $border-radius;
    border: 1px solid $sub-color-gray-bright;

    &:focus {
      outline: 0;
      border-color: $sub-color-black-light; } }

  select {
    display: block;
    margin-top: 4px;
    padding: 3px 32px 3px 6px;
    width: 240px;
    appearance: none;
    @extend %font-size-ui-s;
    border: 1px solid $sub-color-gray-bright;
    border-radius: $border-radius;
    background: $base-color-white resolve('ui-arrow-down.svg') center right no-repeat;

    &:focus {
      outline: 0;
      border-color: $sub-color-black-light; } }

  .data {
    margin-top: 0;

    &.check {
      &::after {
        content: '';
        display: inline-block;
        vertical-align: sub;
        margin-left: 8px;
        width: 20px;
        height: 20px;
        background: resolve('icon-check.svg') no-repeat;
        opacity: .3; } }

    &.checked {
      &::after {
        opacity: 1; } }

    &.important {
      color: $main-color; } }

  .button {
    @extend %button;
    @extend %button-size-s;
    @extend %button-secondary; }

  .input-group, .data-group, .image-group, .check-item, .radio-item {
    margin-top: 4px;

    input, .data {
      display: inline-block;
      margin-right: 8px; }

    .small {
      width: 48px;
      margin-right: 8px; }

    .medium {
      width: 128px;
      margin-right: 8px; }

    .button {
      display: inline-block;
      @extend %button-size-form; } }

  .check-group {
    .check-item {
      display: inline-block;
      margin-right: 16px;

      input[type='checkbox'] {
        margin-right: 4px; }

      &.fixed-width {
        max-width: 180px;
        width: 100%; } } }

  .radio-group {
    .radio-item {
      display: inline-block;
      margin-right: 16px;

      input[type='radio'] {
        margin-right: 4px; }

      &.fixed-width {
        max-width: 180px;
        width: 100%; } } }

  .image-uploader {
    display: inline-flex;
    align-items: flex-end;
    justify-content: center;
    margin-right: 8px;
    width: 100px;
    height: 100px;
    background-position: center;
    background-size: cover;
    border-radius: $border-radius;

    @include tablet {
      width: 100px;
      height: 100px; }

    .upload-hidden {
      display: none; } }

  .small-button {
    display: inline-block;
    margin: 4px 0;
    @extend %button;
    @extend %button-size-s;
    @extend %button-secondary; }

  .table {
    margin: 24px 0; }

  .label + .table {
    margin: 12px 0 24px; } }

.button-group {
  margin-bottom: 72px;

  button {
    display: inline-block;
    @extend %button;
    @extend %button-size-m;
    @extend %button-secondary; }

  .cta-button {
    @extend %button-primary; }

  @include tablet {
    padding-left: 27px; } }

input[type='checkbox']:disabled {
  appearance: none;
  width: 12px;
  height: 12px;
  background-color: $sub-color-gray-bright;
  border-radius: 2px; }
