.bs-detail {
  .detail-title {
    display: flex;
    flex-wrap: nowrap;
    margin-top: 24px;

    .title-image {
      margin-right: 12px;
      width: 64px;
      height: 64px;
      background-position: center;
      background-size: cover;
      border-radius: $border-radius;

      @include tablet {
        margin-right: 24px;
        width: 100px;
        height: 100px; } }

    .title-content {
      max-width: 720px;
      width: 100%;

      .title {
        margin: 0; }

      .sub-title {
        margin: 0 0 24px;
        @extend %font-size-ui-l;
        font-weight: 500;
        color: $sub-color-gray; }

      .status-tag {
        display: inline-block;
        vertical-align: text-top;
        margin-left: 8px;
        padding: 3px 12px;
        @extend %font-size-ui-s;
        color: $main-color;
        border: 1px solid $main-color;
        border-radius: $border-radius; } } }

  .section-title + .detail-title {
    margin-top: 0; }

  .rating-comment {
    padding: 12px;
    margin-bottom: 8px;
    background-color: $sub-color-gray-light;
    border-radius: $border-radius;

    .meta {
      @extend %font-size-s;
      font-weight: 500;
      color: $sub-color-gray; }

    .content {
      margin: 8px 0 0; } } }
